* {
  box-sizing: border-box;

  &:before,
  &:after {
    box-sizing: border-box;
  }
}


button,
input,
optgroup,
select,
textarea {
  font-size: 100%;
  margin: 0;
}


button,
input {
  overflow: visible;
}


button[disabled] {
  opacity: 0.55;
  cursor: not-allowed;
}


[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}


[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}


textarea {
  overflow: auto;
}


p,
h1, h2, h3, h4, h5, h6,
hr {
  margin: 0;
  padding: 0;
}


ul {
  margin: 0;
  padding: 0;


  li {
    list-style: none;
  }
}


a {
  outline: none;
  text-decoration: none;


  &:hover,
  &:focus {
    text-decoration: none;
  }
}


input[type="text"] {
  appearance: none;
}

picture {
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
